/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import "../../../../../Constant/breakPoint.scss";
@import "../../../../../Constant/font.scss";
@import "../../../../../Constant/color.scss";
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.contactPhone_wrapper {
    position: relative;
    top: 0;
    left: 0;
    margin-bottom: 3rem;
    > div:nth-child(2) {
        background-color: $background;
    }
}

.contactPhone_name {
    font: $fs_r_12;
    color: $neutral_600;
    line-height: 2rem;
    margin-bottom: 0.5rem;
}

.contactPhone_ipt_errormsg {
    position: absolute;
    bottom: -2rem;
    left: 0;
    color: $danger_600;
}

// cn style
.contactPhone_cn {
    .contactPhone_name {
        @include c_body_3;
    }
    input[class*="mobileInputV2_ipt"] {
        @include c_body_3;
        &::placeholder {
            color: $neutral_300;
        }
    }
}
